<template>
    <div>
        <my-nav :login="login" :outLogin="outLogin"></my-nav>
        <div class="allBook-top">
            <div>
                <router-link to="/"><img src="image/index/logo.png" alt=""></router-link>
            </div>
            <div class="intStyle">
                <input type="text" v-model="search" @keyup.13="searchAllBook"><span @click="searchAllBook"><i></i><i></i></span>
            </div>
            <div class="imgStyle">
                <a href=""><img src="image/index/header_author.jpg" alt=""></a>
                <a href=""><img src="image/index/header_liwu.jpeg" alt=""></a>
            </div>
        </div>
        <hr style="margin-top:50px;"/>
        <div class="container">
            <div class="allBook-Content">
                <div class="contentList" v-for="(p,i) of allList" :key="i">
                    <div>
                        <router-link :to="`/detail/${p.sid}`"><img v-lazy="`${url}${p.imgUrl}`" alt=""></router-link>
                    </div>
                    <div>
                        <div v-text="p.bname"></div>
                        <div v-text="p.author"></div>
                        <div> 
                              
                              
                              
                              
                             
                              
                               
                              
                              
                              
                            </div>
                        <router-link :to="`/detail/${p.sid}`"></router-link>
                    </div>
                </div>
            </div>
            <div class="intr-all">
                <button @click="toAct(-1)" :disabled="leftBtn" v-show="isHidden"></button>
                <ul>
                    <li v-show="liHidden>7">...</li>
                    <li :class="{active:allAct==i}" v-for="i in count" :key="i" @click="changeAct(i)" v-show="liHidden-7<i&&i<liHidden" >{{i}}</li>
                    <li>...</li>
                </ul>
                <button @click="toAct(+1)" :disabled="rightBtn" v-show="isHidden"></button>
            </div>
        </div>
        <my-footer></my-footer>
        <login :spanHid="spanHid" :loginMsg="loginMsg" :log="log" :imgUrl="imgUrl" :changeImg="changeImg" :toLogin="toLogin" v-on:sendValue="(val)=>this.authCode=val" @sendName="(val)=>this.uname=val" @sendPwd="(val)=>this.upwd=val"></login>   <!---->
    </div>
</template>
<script>
import Event from '../Even.js';
import { setTimeout } from 'timers';
export default {
    data(){
        return {
            url:this.$store.state.url,
            arr:this.$store.state.arr,    //
            log:this.$store.state.log,    //
            imgUrl:Math.ceil(Math.random()*6),       //
            authTrue:false,     //
            loginMsg:"",
            uname:"",    //
            upwd:"",     //
            authCode:"", //
            search:"",
            allList:[],
            count:1,
            allAct:1,
            isHidden:true,
            liHidden:7,
            isClick:false,
            // isClick:true,

        }
    },
    watch:{
        search(){
            this.searchAllBook();
        },
        authCode(){
            if(this.arr[this.imgUrl-1]==this.authCode){
                this.authTrue=true;
            }else{
                this.authTrue=false;
            }
        },
        allAct(){
            this.searchAllBook();
        }
    },
    created(){
        // Event.$on("send",(val)=>{
        //     console.log(val)
        //     this.search=val;
        // }) 
        this.search=this.$store.state.search;
    },
    methods:{
        toAct(i){
            if(this.isClick){
                this.allAct+=i;
                this.liHidden+=i;
                this.isClick=false
                console.log(this.liHidden)
            }
        },
        changeAct(i){
            this.allAct=i;
        },
        searchAllBook(){
            this.axios.get("search",{
                params:{
                    search:this.search,
                    pageNum:this.allAct,
                }
            }).then(res=>{
                this.allList=res.data.data.data;
                this.count=res.data.data.count;
                this.isClick=true;
                if(this.count==0){
                    this.isHidden=false;
                }else{
                    this.isHidden=true;
                }
            })
        },
        outLogin(){         //
            this.common.out.call(this)
        },
        login(){            //
            this.logl(); 
        },
        spanHid(){          //
            this.spanHidden();  
        },
        changeImg(){        //
            this.imgUrl++;
            if(this.imgUrl==7){
                this.imgUrl=1; 
            };
        },
        toLogin(){          //
            this.common.userLogin.call(this);
        },
    },
    mounted(){
        this.searchAllBook();
    },
    computed:{
        leftBtn(){
            return this.allAct==1;
        },
        rightBtn(){
            return this.allAct==this.count
        }
    }
}
</script>
<style>
    @import url("../assets/css/allList.css");
</style>