import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueLazyLoad from 'vue-lazyload'


Vue.use(VueLazyLoad)
//js
import common from './common.js'
//jsvue
Vue.prototype.common=common

//element ui 
import Element from 'element-ui'
Vue.use(Element)
import 'element-ui/lib/theme-chalk/index.css'

//axios
import axios from 'axios'
import qs from 'qs'
//axios
axios.defaults.baseURL="https://xxmyff.applinzi.com/"
axios.defaults.headers.post['Content-Type']='application/x-www-form-urlencoded;charset=UTF-8'
//session
axios.defaults.withCredentials=true
//axiosvue
Vue.prototype.axios=axios
Vue.prototype.qs=qs
Vue.prototype.logl=function(){
  this.log=1;
}
Vue.prototype.spanHidden=function(){
  this.log=null;
}
Vue.prototype.code=function(){
  if(this.arr[this.imgUrl-1]==this.authCode){
    this.authTrue=true;
  }else{
    this.authTrue=false;
  };
}


//
import MyNav from './components/MyNav.vue'

//
import Login from './components/Login.vue'

//
import MyFooter from './components/MyFooter.vue'
//
Vue.component("my-nav",MyNav)
Vue.component("login",Login)
Vue.component("my-footer",MyFooter)
// 
Vue.directive("focus",{
  inserted(e){
    e.focus();
  }
})


// 
router.afterEach((to,from,next) => {
  window.scrollTo(0,0);
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
