export default {
    userLogin(){
        if(this.uname==""&&this.upwd==""){
            this.loginMsg="";
            return;
        }else if(this.uname==""){
            this.loginMsg="";
            return;
        }else if(this.upwd==""){
            this.loginMsg="";
            return;
        }else if(this.authCode==""){
            this.loginMsg="";
            return;
        };
        if(!this.authTrue){
            this.loginMsg="";
        }else{
            this.axios.get("login",{
                params:{
                    uname:this.uname,
                    upwd:this.upwd,
                }
            }).then(res=>{
                if(res.data.code==1){
                    var uname=(res.data.data[0]).slice(-4);
                    var nickName=res.data.data[1]
                    console.log(uname,nickName)
                    sessionStorage.setItem("uname","xxmy"+uname);
                    sessionStorage.setItem("nickName",nickName)
                    this.$alert("","",{confirmButtonText:''}).then(active=>{ 
                        this.log=null;
                        this.$store.getters.getUname;
                        location.reload();
                    }).catch(err=>{
                        this.log=null;
                        location.reload();
                    });
                }else{
                    this.$message.error("");
                }
            });
        }
    },
    out(){
        this.axios.get("outlogin").then(result=>{
            if(result.data.code==1){
                if(this.$route.path==="/center"){
                    this.$router.push("/");
                }else{
                    location.reload();
                }
                this.$message("");
                sessionStorage.removeItem("uname");
            };
        });
    }
 }